header.header {
  display: flex;
  justify-content: space-between;
  padding: 1.4375rem 0 1.125rem 0;
  align-items: center;
}

nav.desktop-nav {
  display: none;
}

ul.desktop-nav__box {
  display: flex;
  width: 100%;
}
ul.desktop-nav__box li {
  list-style: none;
  display: flex;
  align-items: center;
  padding-right: 2em;
}
ul.desktop-nav__box li a {
  font-family: "Manrope" sans-serif;
  font-size: 0.9rem;
  display: flex;
  text-decoration: none;
  padding-right: 0.3em;
  text-transform: uppercase;
  color: #6C6C6C;
  font-weight: 400;
}
ul.desktop-nav__box li:last-child > a {
  color: #F26563;
  font-weight: bold;
}

@media only screen and (min-width: 1200px) {
  nav.mobile-nav--btn {
    display: none;
  }

  nav.desktop-nav {
    display: flex;
  }
}
.submenu-overlay {
  height: 100%;
  width: 100%;
  display: none;
  position: fixed;
  z-index: 11;
  left: 0;
  top: 0;
  background-color: rgba(242, 242, 242, 0.5);
  transition: 0.5s;
}

.submenu-box {
  position: fixed;
  background: #ffffff;
  height: 100%;
  width: 80%;
  z-index: 11;
  display: none;
  border-radius: 10px;
}

.submenu-box__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0.5rem;
}

.submenu-box__close--btn {
  padding: 0.5rem;
}

nav.submenu-box__item a {
  text-transform: uppercase;
  font-size: 1rem;
  color: #6C6C6C;
  padding: 0 0 0.5rem 1rem;
  display: inline-block;
}
nav.submenu-box__item a img {
  padding-left: 0.3rem;
}

.submenu-box.active,
.submenu-overlay.active {
  display: block;
}

.feat-show,
.auto-show {
  display: none;
}

.feat-show.show,
.auto-show.show {
  display: block;
  padding: 0 0.5rem;
  background-color: #F6F2F7;
}
.feat-show.show li,
.auto-show.show li {
  display: block;
}
.feat-show.show li a,
.auto-show.show li a {
  display: block;
  padding: 0.2rem 0.2rem 0.2rem 1.1rem;
  color: #6C6C6C;
  border-bottom: 1px solid #cfcbcb;
}
.feat-show.show li .last-item,
.auto-show.show li .last-item {
  border-bottom: 0;
}

li.active-state a {
  color: #F26563;
}

.footer__container {
