  padding: 0.5rem 0.5rem 0.5rem 0.8rem;
  border: 0;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  width: 58.5%;
  margin: 0 5px 0 0;
  outline: none;
}

button.footer-subscribe {
  padding: 0.5rem;
  border: 0;
  background-color: #F26563;
