  padding: 0 1rem;
  z-index: 10;
  background-color: #ffffff;
}

@media only screen and (min-width: 1200px) {
  .container {
    padding: 0;
  }
}
.mobile-bg {
  background: url("./../src/img/bg-mobile.png") top right no-repeat;
  position: absolute;
  width: 96%;
  height: 100%;
  z-index: -5;
  display: inline-block;
}

.desktop-up-bg {
  display: none;
}

@media only screen and (min-width: 1200px) {
  .mobile-bg {
    display: none;
  }

  .desktop-up-bg {
    background: url("./../src/img/bg-desktop.png") top right no-repeat;
    position: absolute;
    width: 85%;
    height: 100%;
    z-index: -5;
    display: inline-block;
  }
}
.main {
  padding: 0;
}

