.get-started-screen {
  position: relative;
  margin: 0 0 2rem 0;
  height: 70vh;
}

.get-started-screen--bg {
  position: absolute;
  top: 0;
  transform: translateX(50%);
  right: 50%;
  z-index: 5;
}

.get-started-screen__box {
