  border-radius: 30px;
  position: absolute;
  bottom: 0;
  padding: 5.1rem 0 0 0;
}

@media only screen and (min-width: 393px) {
  .get-started-screen__box {
    padding: 11rem 0 0;
  }
}
.get-started-screen__box--heading,
