.get-started-screen__box--buttons {
    padding-bottom: 4rem;
  }
}
@media only screen and (min-width: 1200px) {
  .get-started-screen {
    margin: 2rem 0;
    height: 65vh;
  }

  .get-started-screen__box {
    padding: 12rem 2rem 0rem 2rem;
    transform: translateX(-50%);
    left: 50%;
    width: 65%;
  }

  .get-started-screen__box--heading {
    padding-bottom: 4rem;
  }
  .get-started-screen__box--heading h3 {
    padding-bottom: 2rem;
